import actionTypes from '../actionTypes';

const initialState = JSON.parse(localStorage.getItem('favoriteFilms')) || [];

const favoritesFilmReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case actionTypes.ADD_TO_FAVORITES:
      return [payload.film, ...state];
    case actionTypes.DElETE_FROM_FAVORITES:
      return state.filter(film => film.id !== payload.id);
    default:
      return state;
  }
};
export default favoritesFilmReducer;
