import actionTypes from '../actionTypes';

// eslint-disable-next-line consistent-return
const localStorageSavingsMiddleware = store => next => action => {
  next(action);
  const favoriteFilmsFromState = store.getState().favoritesList;
  const watchedFilmsFromState = store.getState().watchedList;

  switch (action.type) {
    case actionTypes.ADD_TO_FAVORITES:
    case actionTypes.DElETE_FROM_FAVORITES:
      return localStorage.setItem(
        'favoriteFilms',
        JSON.stringify(favoriteFilmsFromState),
      );
    case actionTypes.MARK_WATCHED:
    case actionTypes.DELETE_FROM_WATCHED:
      return localStorage.setItem(
        'watchedFilms',
        JSON.stringify(watchedFilmsFromState),
      );
    default:
  }
};
export default localStorageSavingsMiddleware;
