import actionTypes from '../actionTypes';

const initialState = false;

const loadingFilmsSearchReducer = (state = initialState, { type }) => {
  switch (type) {
    case actionTypes.FILM_LOADING:
      return true;
    case actionTypes.FILM_SEARCH_ERROR:
    case actionTypes.FILM_SEARCH_SUCCESS:
      return false;

    default:
      return state;
  }
};
export default loadingFilmsSearchReducer;
