import { combineReducers } from 'redux';
import loadingFilmsSearchReducer from './filmsSearch/loadingFilmsSearchReducer';
import filmsSearchReducer from './filmsSearch/filmsSearchReducer';
import errorFilmsSearchReducer from './filmsSearch/errorFilmsSearchReducer';
import favoritesListReducer from './userList/favoritesListReducer';
import watchedListReducer from './userList/watchedListReducer';
import setPagesReducer from './filmsSearch/setPagesReducer';
import filmQueryReducer from './filmsSearch/filmQueryReducer';

const combinedReducer = combineReducers({
  isLoading: loadingFilmsSearchReducer,
  films: filmsSearchReducer,
  query: filmQueryReducer,
  pages: setPagesReducer,
  error: errorFilmsSearchReducer,
  favoritesList: favoritesListReducer,
  watchedList: watchedListReducer,
});
export default combinedReducer;
