import actionTypes from '../actionTypes';

const initialState = JSON.parse(localStorage.getItem('watchedFilms')) || [];

const watchedListReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case actionTypes.MARK_WATCHED:
      return [payload.film, ...state];
    case actionTypes.DELETE_FROM_WATCHED:
      return state.filter(film => film.id !== payload.id);
    default:
      return state;
  }
};
export default watchedListReducer;
