import actionTypes from '../actionTypes';

const initialState = {
  activePage: 1,
  totalPages: 0,
};

const setPagesReducer = (state = initialState, { type, payload }) => {
  let total;
  if (payload && payload.totalPages > 1000) {
    total = 1000;
  } else {
    total = payload && payload.totalPages;
  }

  if (payload && payload.totalPages <= 0) {
    total = 1;
  } else {
    total = payload && payload.totalPages;
  }
  switch (type) {
    case actionTypes.SET_PAGES:
      return {
        ...state,
        totalPages: total,
      };
    case actionTypes.SET_ACTIVE_PAGE:
      return {
        ...state,
        activePage: payload.activePage,
      };
    default:
      return state;
  }
};
export default setPagesReducer;
