import Loadable from 'react-loadable';
import Loader from '../components/Loader/Loader';

const AsyncHome = Loadable({
  loader: () =>
    import('./Home/HomeContainer' /* webpackChunkName: 'home-page' */),
  loading: Loader,
});

const AsyncUserList = Loadable({
  loader: () =>
    import('./UserList/UserList' /* webpackChunkName: 'user-list' */),
  loading: Loader,
});
const AsyncFilmPage = Loadable({
  loader: () =>
    import('./FilmPage/FilmPageContainer' /* webpackChunkName: 'film-page' */),
  loading: Loader,
});

const AsyncWatchedFilmsPage = Loadable({
  loader: () =>
    import(
      './UserList/WatchedList/WatchedListContainer' /* webpackChunkName: 'watched-page' */
    ),
  loading: Loader,
});

const AsyncFavoriteFilmsPage = Loadable({
  loader: () =>
    import(
      './UserList/FavoritesList/FavoritesListContainer' /* webpackChunkName: 'favorites-page' */
    ),
  loading: Loader,
});

const routes = [
  {
    exact: true,
    path: '/',
    component: AsyncHome,
  },
  {
    exact: false,
    path: '/list',
    component: AsyncUserList,
  },
  {
    exact: false,
    path: '/:title/:id',
    component: AsyncFilmPage,
  },
  {
    exact: false,
    path: '/list/watched',
    component: AsyncWatchedFilmsPage,
  },
  {
    exact: false,
    path: '/list/favorites',
    component: AsyncFavoriteFilmsPage,
  },
];
export default routes;
